/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 100%;
  max-width: 37.5rem;
  margin: 0 auto;
  min-height: 100vh;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  background-color: hsl(0deg, 0%, 100%);
}
.contenedor .tip-form {
  margin: 0 auto;
  padding: 2rem;
}
@media (min-width: 769px) {
  .contenedor {
    display: flex;
    align-items: center;
    min-height: 100%;
    max-width: fit-content;
    border-radius: 2rem;
  }
}

body {
  background-color: hsl(185deg, 41%, 84%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Space Mono", monospace;
}

.splitter {
  font-size: 3.2rem;
  color: hsl(183deg, 100%, 15%);
  letter-spacing: 1rem;
  font-weight: 700;
}
@media (min-width: 769px) {
  .splitter {
    margin: 10rem 0;
  }
}

.error {
  color: red;
  text-align: right;
  font-family: "Space Mono", monospace;
  font-size: 1.2rem;
}

