.tip-form {
  display: flex;
  flex-direction: column;
}
.tip-form fieldset {
  border: none;
  padding: 0;
  margin-bottom: 2rem;
}
@media (min-width: 769px) {
  .tip-form fieldset:last-of-type {
    margin-bottom: 3.2rem;
  }
}
.tip-form label, .tip-form legend {
  color: hsl(183deg, 100%, 15%);
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.8rem;
}

.tip-select {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 769px) {
  .tip-select {
    grid-template-columns: repeat(3, 1fr);
  }
}
.tip-select label {
  padding: 1rem;
  margin: 0 0.5rem 1.2rem 0.5rem;
  text-align: center;
  font-size: 2.4rem;
  background-color: hsl(183deg, 100%, 15%);
  color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
}
.tip-select label:hover {
  background-color: hsl(184deg, 14%, 56%);
}
.tip-select input[type=text] {
  text-align: right;
  font-size: 2.4rem;
  max-width: 15.55rem;
  max-height: 4.759rem;
  margin-left: 0.5rem;
  padding-right: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: hsl(189deg, 41%, 97%);
}
.tip-select input[type=text]:focus {
  outline: none;
}
.tip-select input[type=text]::placeholder {
  font-size: 2.4rem;
  text-align: right;
  margin-right: 1rem;
  color: hsl(183deg, 100%, 15%);
}
.tip-select input[type=radio] {
  display: none;
}
.tip-select input[type=radio]:checked + label {
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}

.number-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(189deg, 41%, 97%);
  padding: 1rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 769px) {
  .number-input {
    margin-bottom: 3.2rem;
  }
  .number-input:last-of-type {
    margin-bottom: 0;
  }
}
.number-input input[type=text] {
  border: none;
  background-color: inherit;
  font-size: 2.4rem;
  font-weight: 700;
  max-height: 4.9rem;
  text-align: right;
}
.number-input input[type=text]::placeholder {
  font-size: 2.4rem;
  text-align: right;
  color: hsl(184deg, 14%, 56%);
}
.number-input input[type=text]:focus {
  outline: none;
}

#tip-summary {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 2rem;
  border-radius: 1rem;
  background-color: hsl(183deg, 100%, 15%);
}
#tip-summary .amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 769px) {
  #tip-summary .amount {
    gap: 10rem;
  }
  #tip-summary .amount__title {
    margin-left: 2rem;
  }
  #tip-summary .amount__number {
    margin-right: 2rem;
  }
  #tip-summary .amount__number span {
    font-size: 4.2rem;
  }
}
#tip-summary .amount__title {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 0.05rem;
}
#tip-summary .amount__title span {
  font-size: 1.2rem;
  color: hsl(184deg, 14%, 56%);
}
#tip-summary .amount__number {
  color: hsl(172deg, 67%, 45%);
  font-weight: 700;
  font-size: 3rem;
}
#tip-summary .total {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 769px) {
  #tip-summary .total {
    gap: 10rem;
  }
  #tip-summary .total__title {
    margin-left: 2rem;
  }
  #tip-summary .total__number {
    margin-right: 2rem;
  }
  #tip-summary .total__number span {
    font-size: 4.2rem;
  }
}
#tip-summary .total__title {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 0.05rem;
}
#tip-summary .total__title span {
  font-size: 1.2rem;
  color: hsl(184deg, 14%, 56%);
}
#tip-summary .total__number {
  color: hsl(172deg, 67%, 45%);
  font-weight: 700;
  font-size: 3rem;
}

#reset {
  padding: 1.5rem;
  font-size: 2.2rem;
  font-weight: 700;
  color: hsl(183deg, 100%, 15%);
  text-transform: uppercase;
  background-color: hsl(172deg, 67%, 45%);
  border-radius: 1rem;
  border: none;
}
@media (min-width: 769px) {
  #reset {
    margin-top: 10rem;
  }
}
#reset:hover {
  background-color: hsl(185deg, 41%, 84%);
}